.icon{
    height: 40px;
    width: 55px;
    align-self: center;
  }

.heading{
  font-size: 1.7rem;
  color: var(--color--Red);
  
}

.textTop{
  font-size: 1.2rem;
  color: var(--color--Red);
  
  display: grid;
  grid-template-rows: repeat( 3 , min-content);
  justify-items: center;

}
.textBottom{
    font-size: 1.2rem;
    color: var(--color--Red);
   text-align: center;
  }

.details{
  display: grid;
  grid-template-rows: 1fr min-content 1fr;
  justify-items: center;
  grid-row-gap: 1rem;
}

